import { Component } from '@angular/core';
import { Thin2Logger } from 'thin2-log';

@Component({
    selector: 'demo-context-menu',
    templateUrl: './context-menu-view.component.html'
})
export class ContextMenuViewComponent {
    public actions = [
        {
            mode: 'link',
            label: 'Añadir',
            id: 'add_button'
        },
        {
            mode: 'link',
            label: 'Borrar',
            id: 'button_remove'
        },
        {
            mode: 'link',
            label: 'Seleccionar',
            id: 'button_select'
        },
        {
            mode: 'link',
            label: 'Haz click',
            id: 'click_button'
        }
    ];

    public actions2 = [
        {
            mode: 'link',
            label: 'Añadir',
            id: 'add_button'
        },
        {
            mode: 'link',
            label: 'Borrar',
            id: 'button_remove'
        },
        {
            mode: 'link',
            label: 'Seleccionar',
            id: 'button_select'
        },
        {
            mode: 'link',
            label: 'Haz click',
            id: 'click_button'
        }
    ];

    public actions3 = [
        {
            mode: 'link',
            label: 'En el caso que se pase un label muy largo, sucederá esto.',
            id: 'large_button'
        }
    ];

    public actionsDown = [
        {
            mode: 'link',
            label: 'Añadir',
            id: 'add_button'
        },
        {
            mode: 'link',
            label: 'Borrar',
            id: 'button_remove'
        },
        {
            mode: 'link',
            label: 'Seleccionar',
            id: 'button_select'
        },
        {
            mode: 'link',
            label: 'Haz click',
            id: 'click_button'
        }
    ];

    constructor(private LOGGER: Thin2Logger) {}

    public doClick(id: any) {
        switch (id) {
            case 'add_button':
                {
                    this.LOGGER.info('Se ha pulsado el boton añadir');
                }
                break;
            case 'button_remove':
                {
                    this.LOGGER.info('Soy el link de borrar');
                }
                break;
            case 'button_select':
                {
                    this.LOGGER.info('Se ha pulsado el botón de seleccionar');
                }
                break;
        }
    }
}
